<!--饼状图-->
<template>
  <div class="pie-style">
    <div :id="id" class="charts"></div>
  </div>
</template>

<script>
/**
 * @饼状图
 * @author：risheng.huang
 * @参数说明：
 * pieChartsData{
 *     dataMap:[name:"",value:''],    /对应的数据值(百分比)
 *     color:['#31fff1','#27ccc1'], //第一个颜色渐变0颜色和百分比数据颜色，第二个渐变1颜色
 * }  
 * roseType:true  //是否展示成南丁格尔图,默认为是
 * labelType:'label'//是否展示 ouline:连接线展示   emphasis:圆心展示 
 * legendShow:false,//是否展示图例
 * legendRight:'0',//图例离右边位置
 * legendTop:'0',//图例离顶部位置
 * radius: ['15%', '80%']饼图的半径
 * center   :['center', 'center'] 饼图中心位置
 * labelHide:fasle,//是否隐藏label(当数据值小于5%时)
 * */
import echarts from 'echarts';

export default {
  name: "bda-pie-charts",
  props: {
    pieChartsData: {
      type: Object,
      default: () => {
        return {}
      }
    },
    roseType: {
      type: Boolean,
      default: true
    },
    labelType: {
      type: String,
      default: 'ouline'
    },
    legendShow: {
      type: Boolean,
      default: false
    },
    labelHide: {
      type: Boolean,
      default: false
    },
    radius: {
      type: Array,
      default: () => {
        return ['15%', '80%']
      }
    },
    center: {
      type: Array,
      default: () => {
        return ['center', 'center']
      }
    },
    minAngle: {
      type: Number,
      default: 0
    },
    legendRight: {
      type: Number,
      default: 0
    },
    legendTop: {
      type: Number,
      default: 0
    },
  },
  data() {
    return {
      id: Math.random() + '_',
      pieData: [],
      series: [],
      legendData: [],
    }
  },
  watch: {},
  created() {
  },
  mounted() {
    this.dataFilter(this.pieChartsData);
    this.init();
  },
  methods: {
    dataFilter(data) {
      window.console.log(data, 'data');
      //   let that = this
      if (data.dataMap) {

        let legendData = []
        let series = [];

        data.dataMap.forEach(e => {
          legendData.push(e.name)
        });
        this.legendData = legendData;
        this.pieData = [...data.dataMap];
        let that = this
        // let pieData = this.pieData
        // let pieDataAfter = this.labelHide ? this.dealLabelData(pieData) : pieData
        // window.console.log(pieDataAfter, 'pieDataAfter');
        // let that = this
        series.push(
          {
            name: ' ',
            type: 'pie',
            radius: this.radius,
            center: this.center,
            data: this.pieData,
            // pieData.sort(function (a, b) { return a.value - b.value; })
            roseType: this.roseType ? 'radius' : '',
            // roseType: this.roseType ? 'area' : '',
            avoidLabelOverlap: true,
            hoverAnimation: !this.labelHide,
            minAngle: this.minAngle,
            itemStyle: {
              normal: this.roseType ? {
                shadowBlur: 200,
                shadowColor: 'rgba(0, 0, 0, 0.5)',
                color: function (params) {
                  var colorList = [
                    ...data.color
                  ]
                  return new echarts.graphic.LinearGradient(1, 0, 0, 0, [{ //颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
                    offset: 0,
                    color: colorList[params.dataIndex] ? colorList[params.dataIndex][0] : colorList[colorList.length - 1][0]
                  },
                  //   {
                  //     offset: 0.5,
                  //     color: colorList[params.dataIndex][0]
                  //   },
                  {
                    offset: 1,
                    color: colorList[params.dataIndex] ? colorList[params.dataIndex][1] : colorList[colorList.length - 1][1]
                  }
                  ])
                  /*  return colorList[params.dataIndex]*/
                }
              } : {},
            },
            label: this.labelType === 'ouline' ?
              {
                color: '#fff',
                position: 'outer',
                alignTo: 'labelLine',
                bleedMargin: 5,
                formatter: function (params) {
                  if (that.roseType) {
                    return ['{a|' + params.name + '}', '{b|' + params.percent + '%}', '{c|' + '' + '}'].join('\n')
                  } else {
                    return ['{a|' + params.name + '}' + '{b|' + params.percent + '%}']
                  }
                },
                textStyle: {
                  //在rich中给formatter添加个别字体颜色
                  rich: {
                    a: {
                      color: this.roseType ? '#fff' : '',
                      fontSize: 16,
                      fontFamily: 'Noto Sans SC',
                    },
                    b: {
                      color: this.roseType ? '#03a9f4' : '',
                      // padding: [0, 10, 5, 0],
                      fontSize: 16,
                      fontFamily: 'Noto Sans SC',
                    },
                  },
                },
              } : {
                show: false,
                position: 'center',
              },
            emphasis: this.labelType === 'emphasis' ? {
              label: {
                show: true,
                fontWeight: 'bold',
                fontFamily: 'Noto Sans SC',
                formatter: function (params) {
                  let name1 = params.name.substring(0, 6)
                  let name2 = params.name.substring(6, 12)
                  return name2 ? ['{a|' + params.percent + '%}', '{b|' + name1 + '}', '{c|' + name2 + '}'].join('\n') : ['{a|' + params.percent + '%}', '{b|' + name1 + '}'].join('\n');
                },
                textStyle: {
                  //在rich中给formatter添加个别字体颜色
                  rich: {
                    a: {
                      color: '#fff',
                      fontSize: 20,
                    },
                    b: {
                      fontSize: 16,
                    },
                    c: {
                      fontSize: 16,
                    },
                  },
                },
              }
            } : {},
            labelLine: {
              show: this.labelType === 'ouline' ? true : false,
              // length: 0,
              // length2: 0,
              lineStyle: {
                width: 3,
              }
            },

          }
        );
        if (!this.roseType) {
          let colorList = [...data.color]
          series[0].color = colorList
        } else {
          series[0].labelLine.length = 10
          // series[0].labelLine.length2 = 0
        }
        this.series = series;
      }
    },
    init() {
      let chartsDom = document.getElementById(this.id);
      let charts = echarts.init(chartsDom);
      let options =
      {
        tooltip: {
          show: true,
          trigger: 'item',
          formatter: '{b} : {c} ({d}%)',
        },
        legend:
        {
          show: this.legendShow,
          orient: 'vertical',
          selectedMode: 'multiple',
          icon: 'circle',
          right: '0%',
          top: 'center',
          formatter: function (params) {
            let tip1 = "";
            let tip = "";
            let le = params.length  //图例文本的长度
            let over = 6
            if (le > over) {   //几个字换行大于几就可以了
              let l = Math.ceil(le / over)  //有些不能整除，会有余数，向上取整
              for (let i = 1; i <= l; i++) { //循环
                if (i < l) { //最后一段字符不能有\n
                  tip1 += params.slice(i * over - over, i * over) + '\n'; //字符串拼接
                } else if (i === l) {  //最后一段字符不一定够over个
                  tip = tip1 + params.slice((l - 1) * over, le) //最后的拼接在最后
                }
              }
              return tip;
            } else {
              tip = params  //前面定义了tip为空，这里要重新赋值，不然会替换为空
              return tip;
            }
          },
          data: this.legendData,
          textStyle: {
            fontSize: 16,
            color: '#66AAFF',
            fontFamily: 'Noto Sans SC',
          },
        },
        series: this.series
      };
      if (this.legendRight !== 0) {
        let positionRight = this.legendRight
        let positionTop = this.legendTop === 0 ? 'center' : this.legendTop + '%'
        options.legend = []
        // this.legendData.forEach((e, i) => {
        //         options.legend.push({
        //     show: this.legendShow,
        //     orient: 'horizontal',
        //     icon: 'circle',
        //     left: i % 2 === 0 ? positionLeft1 + '%' : positionLeft2 + '%',
        //     top: positionTop + '%',
        //     data: [e],
        //     textStyle: {
        //       fontSize: 16,
        //       color: '#66AAFF',
        //       fontFamily: 'Noto Sans SC',
        //     },
        //   })
        //      if (i % 2 !== 0) positionTop = positionTop + 20
        // })
        options.legend.push({
          show: this.legendShow,
          orient: "vertical",
          icon: "circle",
          left: positionRight - 40 + '%',
          top: positionTop,
          data: this.legendData.splice(0, 3),
          textStyle: {
            fontSize: 16,
            color: "#66AAFF",
            fontFamily: "Noto Sans SC",
          },
        })
        if (this.legendData.length !== 0) {
          options.legend.push({
            show: this.legendShow,
            orient: "vertical",
            icon: "circle",
            left: positionRight - 10 + '%',
            top: positionTop,
            data: this.legendData.splice(0, 3),
            textStyle: {
              fontSize: 16,
              color: "#66AAFF",
              fontFamily: "Noto Sans SC",
            },
          })
        }
      }

      window.console.log(options, 'options111');
      charts.setOption(options)
      // 默认高亮
      if (this.labelType === 'emphasis') {
        // charts.on('legendselectchanged', function (a) {
        //   window.console.log(a, 'a');
        //   charts.dispatchAction({
        //     type: "downplay",
        //     seriesIndex: 0,
        //     dataIndex: index
        //   });
        // });
        let index = 0; // 高亮索引
        charts.dispatchAction({
          type: "highlight",
          seriesIndex: index,
          dataIndex: index
        });
        charts.on("mouseover", function (e) {
          if (e.dataIndex != index) {
            charts.dispatchAction({
              type: "downplay",
              seriesIndex: 0,
              dataIndex: index
            });
          }
        });
        charts.on("mouseout", function (e) {
          index = e.dataIndex;
          charts.dispatchAction({
            type: "highlight",
            seriesIndex: 0,
            dataIndex: e.dataIndex
          });
        });
      }
    },
    dealLabelData(data) {
      let total = 0
      let dealData = data
      dealData.forEach(e => {
        total = total + e.value
      })
      dealData.forEach(e => {
        window.console.log(e.value / total * 100, 's');
        if (e.value / total * 100 < 5) {
          e.label = { show: false }
          e.labelLine = { show: false }
          e.emphasis = e.emphasis ? e.emphasis : {}
          e.emphasis.labelLine = {
            show: false
          }
        }
      })
      return dealData
    }
  }
}
</script>

<style scoped lang="scss">
.pie-style {
  width: 100%;
  height: 100%;
  position: relative;

  .charts {
    width: 100%;
    height: 100%;
  }
}
</style>